import React, { Component } from 'react'
import { Button, Modal } from 'react-bootstrap';

class AddForm extends Component {
    constructor() {
        super();
        this.handleButton = this.handleButton.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleChangeRadio = this.handleChangeRadio.bind(this);
        this.state = {"dd":"importer"};
    }

    handleChange = (event) => {
        console.log(this.state);
        let x = event.target.name;
        this.setState({ [x]: event.target.value });
    }

    handleChangeRadio = (event) => {
        console.log(this.state);
        let x = event.target.name;
        this.setState({ [x]: event.target.id });
    }


    handleButton(e) {

        
        var array = []

        this.props.headings.map((heading) => {
            console.log(heading)
            if(heading == "CurrentOwner"){
                array.push("resource:org.trade.mynetwork."+this.state[heading])
            } else{
            array.push(this.state[heading])
            }
        })
        array.pop();
        array.push(new Date().toUTCString());

        this.props.data.push(array);

        this.props.sd(this.props.data);

        console.log(this.props.data);
        this.props.hc();
        e.preventDefault();
    }


    render() {
        return (
            <div>
                <form>
                    {this.props.headings.map((headings) => {

                        if (headings != "Timestamp") {
                            return (

                                <div class="form-group">
                                    <label for="exampleInputEmail1">{headings}</label>
                                    <input type="text" class="form-control" name={headings} id={headings} aria-describedby="emailHelp" placeholder=" " require onChange={this.handleChange} />
                                </div>
                            )
                        }

                    })}
              

                    {this.props.title == "Issue Letter of Credit" || this.props.title == "Delivery Status"? <div class="form-group">
                                    <label for="exampleInputEmail1">User Type</label><select class="form-control" name="dd" onChange={this.handleChange}>
                        <option>Importer</option>
                        <option>Exporter</option>
                        <option>Importer Bank</option>
                        <option>Exporter Bank</option>
                        <option>Regulatory Authority</option>
                        <option>Shipping Company</option>
                    </select> 
                    
                    </div>

                    : null}
      <br /><br />
                    <div className="row justify-content-end">

                        <div className="col-2">
                            <Button variant="secondary" onClick={this.props.hc}>
                                Cancel
                            </Button>
                        </div>
                        <div className="col-6">
                            { (this.props.title == "Issue Letter of Credit" || this.props.title == "Delivery Status")? this.state.dd == "Importer Bank" ?<Button variant="primary" onClick={this.handleButton}>
                                Add {this.props.title}
                            </Button>:<div><Button variant="primary"disabled  onClick={this.handleButton}>
                                Add {this.props.title}
                            </Button><br/><p className="text-danger">Incorrect user type</p></div>: <Button variant="primary" onClick={this.handleButton}>
                                Add {this.props.title}
                            </Button>}
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}
export default AddForm