import React from 'react';

import { FaQuestionCircle, FaGithub, FaChrome, FaLink } from 'react-icons/fa'


const Info = () => {
    return (
        <div className="info text-left row justify-content-center" id="info">
            <div className="AboutContent col-10">
                <h3>Project Background</h3>
                <p>
                Trade finance represents the financial instruments companies and big banks use to engage in international trade. In a normal supply chain, the importer faces the risk that they will make a payment but not receive the required goods while the exporter runs the risk that they will deliver the goods without sending payment. Therefore, there is a lot of distrust in this industry. The manual processing of paper documentation is a major problem facing large financial institutions and especially those in this sector. When trade finance is applied to the pharmaceutical industry there is a greater strain placed on the system due to the strict regulation enforced by governing bodies. This application allows for all information to be shared to the blockchain network and visible to all participants resulting in efficient processes and better relationships.

            </p>
<br/>

                <h3>Project Implementation</h3>
                <p>The project is implemented using the Hyperledger Composer tool that allows for permissioned blockchain solutions to be set up quickly and used as a proof of concept for the existing enterprise use case.
While a basic skeleton front end is provided with Composer, React was chosen instead due to prior experience with the technology.
.</p>
<br/>

                <h3>Contributors</h3>
                <br />

                <div className="row">
                    <div className="col">
                        <h4>Diarmaid Tully</h4>
                        <p>Project Developer</p>
                        
                    </div>
                    <div className="col">
                        <h4>Marco Ruffini</h4>
                        <p>Project Supervisor</p>
                        
                    </div>
                    <div className="col">
                        <h4>Nima Afraz</h4>
                        <p>Assistant Professor</p>
                        
                    </div>

                </div>
            </div>



        </div>
    );
};



export default Info;