import { Button, Modal } from 'react-bootstrap';
import React, { useReducer, useState } from "react";
import {FaEdit, FaTrashAlt} from 'react-icons/fa';
import AddForm from './AddForm';
import TransactionForm from './TransactionForm';

function makeid(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * 
 charactersLength));
   }
   return result;
}

const Transaction = (props) => {

   
    const [show, setShow] = useState(false);
    const [data, setData] = useState(props.data);
    const[tl,setTl] = useState(props.data);


    const handleClose = () => {setShow(false); forceUpdate();}
    const handleShow = () => setShow(true);

    const [ignored, forceUpdate] = useReducer(x => x + 1, 0);

    console.log(data)

    return (
        <div>
           

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title> Add {props.title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>

              <TransactionForm headings={props.headings} data={props.data} hc={handleClose} title={props.title} sd={setData} />
                </Modal.Body>

                <Modal.Footer>
                    
                </Modal.Footer>
            </Modal>


                
                <div className="row">
                <div className="col-9">
                    <h1>{props.title}</h1>
                </div>
                <div className="col-3">
                   
                </div>
            </div>  <br /> <br />

            <p>{props.text}</p> <br/>

            <p>When invoked the transaction will have the following attributes:</p>
            <ul>
            {props.headings.map((heading)=>{
                return(<li>{heading}</li>)
            })}
            </ul>
            <br/>
            <Button variant="primary" onClick={handleShow}>
                        Add {props.title}
                    </Button>

                <br/><br/><br/>
                <h1>Transaction log</h1>


                <table class="table">
                    <thead>
                        <tr>
                            <th>Transaction ID</th>
                            {props.headings.map((headings) => {
                                return (<th scope="col">{headings}</th>)
                            })}
                            
                        </tr>
                    </thead>

                    <tbody>

                        {data.map((product,i) => {

                            return (
                                <tr>
                                    <td>{makeid(32)}</td>
                                    {product.map((item) => {
                                        return (<td>{item}</td>)
                                    })
                                    }
                                    
                                </tr>
                            )
                        })}


                    </tbody>
                </table>

        </div>
            )
}

            export default Transaction;

